import React from "react";
import { Route } from "react-router-dom";
import { validation } from "./validation";
import Registration from "../../layouts/LoggedOutLayout/Registration/Registration"

const PrivateRoute: React.FC<{ component: any; path: any; }> = ({ component, path }) => {

    const condition = validation();
    console.log(condition)

    return !!condition ? (<Route path={path} element={component} />) :
        (<Route path={path} element={<Registration />} />);
};
export default PrivateRoute;